.frozen-container {
  padding: 16px;
}

/* Frozen details header styles removed - using unified page-header from common.css */

.frozen-description {
  background-color: #f5f7fa;
  padding: 12px;
  border-radius: 8px;
  margin-bottom: 16px;
}

.frozen-description p {
  margin: 0;
  font-size: 14px;
  color: #666;
  line-height: 1.5;
}

.frozen-list {
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 16px;
}

.frozen-record {
  padding: 8px 0;
}

.frozen-record-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.frozen-record-amount {
  font-size: 18px;
  font-weight: 500;
  color: #ff8f1f;
}

.frozen-record-info {
  font-size: 14px;
  color: #666;
  line-height: 1.5;
}

.empty-records {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  color: #999;
}

/*  */
.frozen-detail {
  padding: 8px 0;
}

.detail-item {
  margin-bottom: 12px;
  display: flex;
  flex-direction: column;
}

.detail-label {
  font-size: 14px;
  color: #666;
  margin-bottom: 4px;
}

.detail-value {
  font-size: 16px;
  color: #333;
}

.detail-value.frozen {
  color: #ff8f1f;
  font-weight: 500;
}

/*  */
@media (max-width: 480px) {
  .frozen-container {
    padding: 12px;
  }
  
  .frozen-description {
    padding: 10px;
    margin-bottom: 12px;
  }
  
  .frozen-description p {
    font-size: 13px;
  }
  
  .frozen-list {
    margin-bottom: 12px;
  }
  
  .frozen-record-amount {
    font-size: 16px;
  }
  
  .frozen-record-info {
    font-size: 13px;
  }
  
  .empty-records {
    height: 150px;
    font-size: 13px;
  }
  
  .detail-label {
    font-size: 13px;
  }
  
  .detail-value {
    font-size: 15px;
  }
}

@media (max-width: 320px) {
  .frozen-container {
    padding: 8px;
  }
  
  .frozen-description {
    padding: 8px;
  }
  
  .frozen-description p {
    font-size: 12px;
  }
  
  .frozen-record-amount {
    font-size: 15px;
  }
  
  .frozen-record-info {
    font-size: 12px;
  }
  
  .empty-records {
    height: 120px;
    font-size: 12px;
  }
  
  .detail-label {
    font-size: 12px;
  }
  
  .detail-value {
    font-size: 14px;
  }
}