.order-center-container {
  padding: 16px;
}

.order-status-flow {
  padding: 16px 0;
}

/* Order center header styles removed - using unified page-header from common.css */

/* Order center header styles removed - using unified page-header from common.css */

.export-button {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 6px 12px;
  background-color: #1677ff;
  color: white;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  white-space: nowrap;
}

.order-search {
  margin-bottom: 16px;
  flex: 1;
  position: relative;
  display: flex;
  align-items: center;
  gap: 12px;
}

.search-controls {
  display: flex;
  align-items: center;
  gap: 12px;
}

.advanced-search-toggle {
  display: flex;
  align-items: center;
  gap: 4px;
  color: #1677ff;
  font-size: 14px;
  cursor: pointer;
  padding: 4px 8px;
  white-space: nowrap;
}

.advanced-search-panel {
  margin-bottom: 16px;
  background-color: #f5f5f5;
  border-radius: 8px;
  padding: 8px;
}

.search-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 16px;
}

.order-actions {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
}

.create-order-btn {
  white-space: nowrap;
  display: flex;
  align-items: center;
  gap: 4px;
}

.order-list {
  background-color: transparent;
}

.order-item {
  width: 100%;
}

.order-item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.order-item-header div:last-child {
  cursor: pointer;
}

.order-id {
  font-weight: 500;
  font-size: 16px;
}

.order-item-info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  font-size: 14px;
  color: #666;
}

/*  */
.order-item-info .full-width-field {
  grid-column: span 2;
}

.order-item-info div {
  display: flex;
  flex-wrap: wrap;
}

/*  */
@media (max-width: 768px) {
  .order-item-info {
    grid-template-columns: 1fr;
  }
  
  .order-item-info div {
    grid-column: span 1 !important;
  }
}

@media (max-width: 480px) {
  .order-center-container {
    padding: 12px;
  }
  
  .order-search {
    margin-bottom: 12px;
  }
  
  .search-controls {
    gap: 8px;
  }
  
  .advanced-search-toggle {
    font-size: 13px;
    padding: 3px 6px;
  }
  
  .order-actions {
    gap: 8px;
    margin-bottom: 12px;
  }
  
  .export-button {
    padding: 5px 10px;
    font-size: 13px;
  }
  
  .create-order-btn {
    font-size: 13px;
  }
  
  .order-id {
    font-size: 15px;
  }
  
  .order-item-info {
    font-size: 13px;
    gap: 6px;
  }
}

@media (max-width: 320px) {
  .order-center-container {
    padding: 8px;
  }
  
  .search-controls {
    flex-direction: column;
    align-items: stretch;
    gap: 8px;
  }
  
  .order-actions {
    flex-direction: column;
    align-items: stretch;
  }
  
  .export-button,
  .create-order-btn {
    justify-content: center;
  }
  
  .order-id {
    font-size: 14px;
  }
  
  .order-item-info {
    font-size: 12px;
  }
}

.order-time {
  /* full-width-fieldgrid-column */
  color: #999;
  font-size: 12px;
  margin-top: 4px;
}

.order-icon {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-weight: bold;
}

.daraz-icon {
  background-color: #ff8f1f;
}

.express-icon {
  background-color: #1677ff;
}