.info-container {
  padding: 16px;
  background-color: #f5f7fa;
  min-height: 100vh;
}

/* Info management header styles removed - using unified page-header from common.css */

.upload-button {
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f5;
  border-radius: 8px;
  color: #999;
}

.upload-button span {
  margin-top: 8px;
  font-size: 14px;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  padding: 0 8px;
}

.section-header h4 {
  margin: 0;
  font-size: 16px;
  color: #333;
}

.section-tip {
  margin-top: 16px;
  padding: 8px 16px;
  background-color: #f0f9ff;
  border-radius: 8px;
  font-size: 14px;
  color: #666;
  line-height: 1.5;
}

.section-tip p {
  margin: 0;
}

.contact-list,
.financial-list {
  background-color: transparent;
  margin-bottom: 16px;
}

.contact-card,
.financial-card {
  width: 100%;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  overflow: hidden;
}

.contact-info,
.financial-info {
  padding: 12px;
}

.contact-name,
.financial-type {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 16px;
  margin-bottom: 8px;
  color: #333;
}

.contact-role {
  margin-left: 8px;
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 12px;
  background-color: rgba(22, 119, 255, 0.1);
  color: #1677ff;
}

.contact-details,
.financial-details {
  font-size: 14px;
  color: #666;
  line-height: 1.5;
}

.contact-details div,
.financial-details div {
  display: flex;
  align-items: center;
  gap: 4px;
  margin-bottom: 4px;
}

.empty-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 48px 16px;
  text-align: center;
}

/*  */
.account-list,
.notification-list,
.security-list {
  background-color: transparent;
  margin-bottom: 16px;
}

.login-info {
  margin-top: 16px;
  padding: 12px 16px;
  background-color: #f5f7fa;
  border-radius: 8px;
  font-size: 14px;
  color: #666;
  line-height: 1.5;
}

.login-info p {
  margin: 4px 0;
}

.security-status {
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 12px;
}

.security-status.active {
  background-color: rgba(82, 196, 26, 0.1);
  color: #52c41a;
}

.security-status.inactive {
  background-color: rgba(245, 34, 45, 0.1);
  color: #f5222d;
}

/*  */
.shop-auth-container {
  padding: 12px;
}

.shop-list {
  background-color: transparent;
  margin-bottom: 16px;
}

.shop-list .adm-list-item {
  padding: 16px 12px;
  margin-bottom: 12px;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
}

.shop-item {
  width: 100%;
}

.shop-title {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 12px;
  gap: 4px;
}

.shop-name {
  font-weight: 600;
  font-size: 17px;
  color: #333;
  line-height: 1.3;
}

.shop-code {
  font-size: 13px;
  color: #999;
  background-color: #f5f7fa;
  padding: 2px 8px;
  border-radius: 4px;
}

.shop-details {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 14px;
}

.shop-detail-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 0;
  border-bottom: 1px solid #f0f0f0;
}

.shop-detail-item:last-child {
  border-bottom: none;
}

.shop-detail-item .label {
  color: #666;
  font-weight: 500;
  min-width: 80px;
  flex-shrink: 0;
}

.shop-detail-item .value {
  color: #333;
  text-align: right;
  flex: 1;
  word-break: break-all;
}

.shop-detail-item .status {
  padding: 4px 12px;
  border-radius: 16px;
  font-size: 12px;
  font-weight: 500;
  white-space: nowrap;
}

.shop-detail-item .status.active {
  background-color: rgba(82, 196, 26, 0.15);
  color: #52c41a;
}

.shop-detail-item .status.inactive {
  background-color: rgba(245, 34, 45, 0.15);
  color: #f5222d;
}

/*  */
@media (max-width: 480px) {
  .shop-auth-container {
    padding: 8px;
  }
  
  .shop-list .adm-list-item {
    padding: 12px 8px;
    margin-bottom: 8px;
  }
  
  .shop-name {
    font-size: 16px;
  }
  
  .shop-code {
    font-size: 12px;
  }
  
  .shop-details {
    gap: 8px;
    font-size: 13px;
  }
  
  .shop-detail-item {
    padding: 6px 0;
  }
  
  .shop-detail-item .label {
    min-width: 70px;
    font-size: 13px;
  }
  
  .shop-detail-item .value {
    font-size: 13px;
  }
}

.auth-dialog-content {
  padding: 16px 0;
}

.auth-dialog-content p {
  margin-bottom: 16px;
}

.auth-instructions {
  margin-top: 24px;
  padding: 16px;
  background-color: #f5f7fa;
  border-radius: 8px;
  border-left: 4px solid #1677ff;
}

.auth-instructions h4 {
  margin-top: 0;
  margin-bottom: 12px;
  font-size: 16px;
  color: #333;
}

.auth-instructions ol {
  margin: 0;
  padding-left: 20px;
}

.auth-instructions li {
  margin-bottom: 12px;
  line-height: 1.6;
  color: #666;
}

.auth-instructions li:last-child {
  margin-bottom: 0;
}

.auth-instructions strong {
  color: #333;
}

/*  */
.address-group {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.address-group .adm-form-item {
  flex: 1;
  min-width: 120px;
}

/*  */
.section-title {
  font-size: 16px;
  font-weight: 500;
  color: #333;
  margin: 16px 0 12px 0;
  padding-left: 8px;
  border-left: 3px solid #1677ff;
}

/*  */
.adm-collapse {
  margin-bottom: 16px;
  border-radius: 8px;
  overflow: hidden;
}

.adm-collapse-panel {
  margin-bottom: 16px;
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
}

.adm-collapse-panel-header {
  font-weight: 500;
}

.adm-collapse-panel-content {
  padding: 16px;
}

/*  */
.adm-swipe-action-actions {
  height: 100%;
}

/*  */
.adm-form-item-label {
  font-weight: 500;
  color: #333;
}

/*  */
.adm-empty {
  padding: 32px 0;
}